import React, { useEffect, useRef } from 'react';
import { StaticImage } from 'gatsby-plugin-image';
import styled from 'styled-components';
import { srConfig } from '@config';
import sr from '@utils/sr';
import { usePrefersReducedMotion } from '@hooks';

import { 
  SiPython, SiR, SiCplusplus, SiMysql, SiLatex, SiGithubactions,
  SiNumpy, SiPandas, SiScipy, SiFlux, SiAnsible, SiApachespark,
  SiJupyter, SiScikitlearn, SiPytorch, SiKeras, SiTensorflow, SiSonarcloud,
  SiOpencv, SiDocker, SiKubernetes, SiGrafana, SiHelm, SiMlflow,
  SiPrometheus, SiElasticsearch, SiHtml5, SiCss3, SiSkaffold, SiTerraform,
  SiJavascript, SiMongodb, SiLinux, SiGit, SiKibana, SiFluentd, SiGnubash
} from 'react-icons/si';

import { LiaJenkins, LiaGitlab } from "react-icons/lia";
import { FaJava, FaReact } from "react-icons/fa";
import { TbSql } from "react-icons/tb";
import { DiNodejs } from "react-icons/di";
import { BiLogoGoogleCloud } from "react-icons/bi";
import { FcScatterPlot } from "react-icons/fc";
import { SiApachehadoop } from "react-icons/si";

import { IconMatplotlib } from '../icons';
import { IconSeaborn } from '../icons';

const StyledAboutSection = styled.section`
  max-width: 900px;

  .inner {
    display: grid;
    grid-template-columns: 3fr 2fr;
    grid-gap: 50px;

    @media (max-width: 768px) {
      display: block;
    }
  }
`;
const StyledText = styled.div`
  ul.skills-list {
    display: grid;
    grid-template-columns: repeat(2, minmax(140px, 200px));
    grid-gap: 0 10px;
    padding: 0;
    margin: 20px 0 0 0;
    overflow: hidden;
    list-style: none;

    li {
      position: relative;
      margin-bottom: 10px;
      padding-left: 20px;
      font-family: var(--font-mono);
      font-size: var(--fz-xs);

      &:before {
        content: '';
        position: absolute;
        left: 0;
        color: var(--green);
        font-size: var(--fz-sm);
        line-height: 12px;
      }
    }
  }
`;
const StyledPic = styled.div`
  position: relative;
  max-width: 300px;

  @media (max-width: 768px) {
    margin: 50px auto 0;
    width: 70%;
  }

  .wrapper {
    ${({ theme }) => theme.mixins.boxShadow};
    display: block;
    position: relative;
    width: 100%;
    border-radius: var(--border-radius);
    background-color: var(--green);

    &:hover,
    &:focus {
      outline: 0;
      transform: translate(-4px, -4px);

      &:after {
        transform: translate(8px, 8px);
      }

      .img {
        filter: none;
        mix-blend-mode: normal;
      }
    }

    .img {
      position: relative;
      border-radius: var(--border-radius);
      mix-blend-mode: multiply;
      filter: grayscale(100%) contrast(1);
      transition: var(--transition);
    }

    &:before,
    &:after {
      content: '';
      display: block;
      position: absolute;
      width: 100%;
      height: 100%;
      border-radius: var(--border-radius);
      transition: var(--transition);
    }

    &:before {
      top: 0;
      left: 0;
      background-color: var(--navy);
      mix-blend-mode: screen;
    }

    &:after {
      border: 2px solid var(--green);
      top: 14px;
      left: 14px;
      z-index: -1;
    }
  }
`;

const StyledSkillsSection = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-top: 2rem;

  @media (max-width: 768px) {
    flex-direction: column;
  }
`;

const SkillColumn = styled.div`
  width: 48%;

  @media (max-width: 768px) {
    width: 100%;
    margin-bottom: 2rem;
  }
`;

const SkillCategory = styled.div`
  margin-bottom: 2rem;
`;

const CategoryName = styled.h3`
  font-size: 1.2rem;
  color: var(--green);
  margin-bottom: 1rem;
`;

const SkillsList = styled.ul`
  list-style-type: none;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
`;

const SkillItem = styled.li`
  display: flex;
  align-items: center;
  margin-right: 1rem;
  margin-bottom: 0.5rem;
`;

const SkillIcon = styled.div`
  font-size: 1.5rem;
  margin-right: 0.5rem;
`;

const SkillName = styled.span`
  font-size: 0.9rem;
`;

const About = () => {
  const revealContainer = useRef(null);
  const prefersReducedMotion = usePrefersReducedMotion();

  useEffect(() => {
    if (prefersReducedMotion) {
      return;
    }

    sr.reveal(revealContainer.current, srConfig());
  }, []);

  const skillCategories = [
    {
      name: "Programming Languages",
      skills: [
        { name: 'Python', icon: SiPython },
        { name: 'R', icon: SiR },
        { name: 'Java', icon: FaJava },
        { name: 'C++', icon: SiCplusplus },
        { name: 'SQL', icon: TbSql },
        { name: 'LaTeX', icon: SiLatex },
      ]
    },
    {
      name: "Data Science & ML Libraries",
      skills: [
        { name: 'NumPy', icon: SiNumpy },
        { name: 'Pandas', icon: SiPandas },
        { name: 'Matplotlib', icon: FcScatterPlot },
        { name: 'Seaborn', icon: FcScatterPlot },
        { name: 'SciPy', icon: SiScipy },
        { name: 'Jupyter', icon: SiJupyter },
        { name: 'Scikit-learn', icon: SiScikitlearn },
        { name: 'PyTorch', icon: SiPytorch },
        { name: 'Keras', icon: SiKeras },
        { name: 'TensorFlow', icon: SiTensorflow },
        { name: 'MLflow', icon: SiMlflow }
      ]
    },
    {
      name: "Computer Vision & NLP",
      skills: [
        { name: 'OpenCV', icon: SiOpencv },
        { name: 'Scikit-image', icon: SiScikitlearn }, 
        { name: 'MONAI', icon: SiPytorch },
        { name: 'Gensim', icon: SiPython }, 
        { name: 'NLTK', icon: SiPython }, 
      ]
    },
    {
      name: "Web Development",
      skills: [
        { name: 'HTML', icon: SiHtml5 },
        { name: 'CSS', icon: SiCss3 },
        { name: 'JavaScript', icon: SiJavascript },
        { name: 'Node.js', icon: DiNodejs },
        { name: 'React', icon: FaReact },
      ]
    },
    {
      name: "DevOps & Cloud",
      skills: [
        { name: 'Docker', icon: SiDocker },
        { name: 'Kubernetes', icon: SiKubernetes },
        { name: 'Google Cloud Platform', icon: BiLogoGoogleCloud },
        { name: 'Jenkins', icon: LiaJenkins },
        { name: 'GitLab CI/CD', icon: LiaGitlab },
        { name: 'Grafana', icon: SiGrafana },
        { name: 'Prometheus', icon: SiPrometheus },
        { name: 'Elasticsearch', icon: SiElasticsearch },
        { name: 'Kibana', icon: SiKibana },
        { name: 'Skaffold', icon: SiSkaffold },
        { name: 'Helm', icon: SiHelm },
        { name: 'Fluentd', icon: SiFluentd },
        { name: 'GitHub Actions', icon: SiGithubactions },
        { name: 'FluxCD', icon: SiFlux },
        { name: 'Terraform', icon: SiTerraform },
        { name: 'Ansible', icon: SiAnsible },
        { name: 'SonarCloud', icon: SiSonarcloud },
      ]
    },
    {
      name: "Databases",
      skills: [
        { name: 'MySQL', icon: SiMysql },
        { name: 'MongoDB', icon: SiMongodb },
      ]
    },
    {
      name: "Big Data & Analytics",
      skills: [
        { name: 'Hadoop', icon: SiApachehadoop },
        { name: 'Spark', icon: SiApachespark },
      ]
    },
    {
      name: "Tools & Operating Systems",
      skills: [
        { name: 'Linux', icon: SiLinux },
        { name: 'Git', icon: SiGit },
        { name: 'Bash', icon: SiGnubash },
      ]
    },
  ];

  const midpoint = Math.ceil(skillCategories.length / 2);

  return (
    <StyledAboutSection id="about" ref={revealContainer}>
      <h2 className="numbered-heading">About Me</h2>

      <div className="inner">
        <StyledText>
          <div>

<p>
  Hello! My name is Adnane El Bouhali, and I'm passionate about developing innovative machine learning solutions. My interest in AI and data science sparked during my engineering studies, where I discovered the power of algorithms to solve complex problems and extract insights from data.
</p>

<p>
  Fast-forward to today, and I'm pursuing a Master's degree at <a href="https://www.ip-paris.fr/en" target="_blank" rel="noreferrer">Institut Polytechnique de Paris</a>, specializing in Data Science and Artificial Intelligence. This program is an integral part of my dual degree in Engineering, spanning both <a href="https://www.telecom-paris.fr/" target="_blank" rel="noreferrer">Tlcom Paris</a> and <a href="https://www.inpt.ac.ma/" target="_blank" rel="noreferrer">Institut National des Postes et Tlcommunications</a>. My academic journey has been marked by excellence, ranking 28th nationally in Morocco during my preparatory classes and graduating top of my class in software engineering at INPT.
</p>

<p>
  I've had the privilege of applying my skills in real-world scenarios, including but not limited to an internship at the <a href="https://www.narsa.gov.ma/" target="_blank" rel="noreferrer">National Agency of Road Safety (NARSA)</a>, where I delved into reinforcement learning for automatic road traffic optimization. This experience, along with my studies at Tlcom Paris, has not only honed my technical skills but also cultivated my sensitivity to the ethical and societal issues raised by AI technologies.
</p>

<p>
  Recently, I've been working on several exciting projects, including <a href="https://github.com/Adnane-ElBouhali/UNet-Cardiac-MRI-Segmentation" target="_blank" rel="noreferrer">UNet Cardiac MRI Segmentation</a> and <a href="https://github.com/Adnane-ElBouhali/Skin-Lesion-Classification" target="_blank" rel="noreferrer">Skin Lesion Classification</a>. These projects have allowed me to apply cutting-edge techniques in deep learning and computer vision to real-world medical imaging challenges.
</p>


            <p>Here are technologies Ive been working with:</p>
          </div>

          <StyledSkillsSection>
      <SkillColumn>
        {skillCategories.slice(0, midpoint).map((category, index) => (
          <SkillCategory key={index}>
            <CategoryName>{category.name}</CategoryName>
            <SkillsList>
              {category.skills.map((skill, skillIndex) => (
                <SkillItem key={skillIndex}>
                  <SkillIcon>
                    <skill.icon />
                  </SkillIcon>
                  <SkillName>{skill.name}</SkillName>
                </SkillItem>
              ))}
            </SkillsList>
          </SkillCategory>
        ))}
      </SkillColumn>
      <SkillColumn>
        {skillCategories.slice(midpoint).map((category, index) => (
          <SkillCategory key={index + midpoint}>
            <CategoryName>{category.name}</CategoryName>
            <SkillsList>
              {category.skills.map((skill, skillIndex) => (
                <SkillItem key={skillIndex}>
                  <SkillIcon>
                    <skill.icon />
                  </SkillIcon>
                  <SkillName>{skill.name}</SkillName>
                </SkillItem>
              ))}
            </SkillsList>
          </SkillCategory>
        ))}
      </SkillColumn>
    </StyledSkillsSection>
        </StyledText>

        <StyledPic>
          <div className="wrapper">
            <StaticImage
              className="img"
              src="../../images/grande_photo.jpg"
              width={500}
              quality={95}
              formats={['AUTO', 'WEBP', 'AVIF']}
              alt="Headshot"
            />
          </div>
        </StyledPic>
      </div>
    </StyledAboutSection>
  );
};

export default About;
